body {
  margin: 0;
  font-family: 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $grey;
  font-size:0.8rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root { 
  display:flex;
  flex-direction:column;
  min-height: 100vh;
  flex-wrap: nowrap;
  font-weight: 400;

  .main-container {
    padding-top: 90px;
    padding-left:120px;
    flex-grow: 1;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
  }

  main {
    flex-grow: 1;
    padding:0 100px;
  }



  h1 {
    font-size: 3rem;
    line-height: normal;
    font-weight: 500;
    margin-top: 30px;
    margin-bottom: 20px;
    color: $secondary;
  }
  h2 {
    font-size: 1.3rem;
    line-height: 2rem;
    color: $secondary;
  }
  h3 {
    font-size: 1rem;
    line-height: normal;
    color: $secondary;
    font-weight: 500;
  }
}

.text{
  &--primary {
    color: $primary!important;
  }

  &--white {
    color: white!important
  }
}

.flex {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  gap:30px;
  align-items: center;
  justify-content: space-between;

  &__row {
    flex-direction: row;
  }

  &__column {
    flex-direction: column;
  }

  &__item {
    height: 100%;
    width: 100%;
  }
}

@media (max-width: 1200px), (max-height:850px) {
  body{
    h1 {
      font-size: 2rem!important;
      line-height: normal!important;
      margin: 15px 0!important;
    }

    h2 {
      font-size: 1rem!important;
      line-height: normal!important;
    }

    h3 {
      font-size: 0.8rem!important;
      line-height: normal!important;
    }

    p{
      font-size: 0.7rem!important;
    }    
  }

  
  main {
    padding:0 20px!important;
  }
}

.main-container {
  padding-top: 90px;
  padding-left:120px;
  flex-grow: 1;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

@media (max-width: 1200px), (max-height:850px)  {
  #root {
    .main-container {
      padding-top: 80px!important;
      padding-left:70px!important;
    }
  }
}